<script>
export default {
  name: 'SWCrawlText',
  computed: {
    isDark() {
      return this.$vuetify.theme.dark
    },
    color() {
      const color = this.isDark
        ? 'hsla(60, 100%, 50%, 0.308)'
        : 'hsla(0, 0%, 0%, 0.308)'

      return `color: ${color}`
    },
  },
}
</script>

